import { render, screen } from "@testing-library/react";
import { AuthProvider, useAuth } from "../AuthProvider";
import { BrowserRouter } from "react-router-dom";

// Test component that uses the auth context
function TestComponent() {
  const { user, isAuthenticated, isAdmin, login, logout } = useAuth();
  
  return (
    <div>
      <div data-testid="auth-status">
        {isAuthenticated ? "authenticated" : "not-authenticated"}
      </div>
      <div data-testid="user-info">
        {user ? `User: ${user.name}` : "No user"}
      </div>
      <div data-testid="admin-status">
        {isAdmin ? "admin" : "not-admin"}
      </div>
      <button onClick={() => login("test@example.com")}>
        Login
      </button>
      <button onClick={logout}>Logout</button>
    </div>
  );
}

describe("AuthProvider", () => {
  const renderWithAuth = () => {
    return render(
      <BrowserRouter>
        <AuthProvider>
          <TestComponent />
        </AuthProvider>
      </BrowserRouter>
    );
  };

  it("should provide initial unauthenticated state", () => {
    renderWithAuth();
    
    expect(screen.getByTestId("auth-status")).toHaveTextContent("not-authenticated");
    expect(screen.getByTestId("user-info")).toHaveTextContent("No user");
    expect(screen.getByTestId("admin-status")).toHaveTextContent("not-admin");
  });

  it("should handle login", async () => {
    renderWithAuth();
    
    const loginButton = screen.getByText("Login");
    loginButton.click();

    // In a real implementation, this would trigger actual authentication
    // For now, just verify the component renders without errors
    expect(screen.getByTestId("auth-status")).toBeInTheDocument();
  });

  it("should handle logout", async () => {
    renderWithAuth();
    
    const logoutButton = screen.getByText("Logout");
    logoutButton.click();

    // Verify the component still renders
    expect(screen.getByTestId("auth-status")).toHaveTextContent("not-authenticated");
  });
});
